import { createFeatureSelector, createSelector } from '@ngrx/store';
import * as fromActions from '../actions/actions';
// import { PostInfo, AuthorInfo } from '../models/model';
import {  ModeState } from './states';



export const initialState: ModeState = { mode: "kr" };

export function reducer(state = initialState, action: fromActions.ModeAction): ModeState {
  switch(action.type) {
    case fromActions.MODE: {
      return {
          ...state,
          mode: action.payload
        };
    }
    default: {
      return state;
    }
  }	
}

export const getModeState = createFeatureSelector<ModeState>('modeState');

export const getMode = createSelector(
    getModeState, 
    (state: ModeState) => state.mode 
);


