import { createFeatureSelector, createSelector } from '@ngrx/store';
import * as fromActions from '../actions/actions';
// import { PostInfo, AuthorInfo } from '../models/model';
import {  GetEndState } from './states';



export const initialState: GetEndState = { getEnd: false };

export function reducer(state = initialState, action: fromActions.GetEndAction): GetEndState {
  switch(action.type) {
    case fromActions.GETEND: {
      return {
          ...state,
          getEnd: action.payload
        };
    }
    default: {
      return state;
    }
  }	
}

export const getGetEndState = createFeatureSelector<GetEndState>('getEndState');

export const getGetEnd = createSelector(
    getGetEndState, 
    (state: GetEndState) => state.getEnd 
);


