import { createFeatureSelector, createSelector } from '@ngrx/store';
import * as fromActions from '../actions/actions';
import { PostInfo, AuthorInfo } from '../models/model';
import { PostState, AuthorState, SelectState } from './states';



export const initialState: SelectState = { selectTag: 'all_all' };

export function reducer(state = initialState, action: fromActions.SelectAction): SelectState {
  switch(action.type) {
    case fromActions.SELECTTAG: {
      return {
          ...state,
          selectTag: action.payload
        };
    }
    default: {
      return state;
    }
  }	
}

export const getSelectState = createFeatureSelector<SelectState>('selectState');

export const getSelect = createSelector(
    getSelectState, 
    (state: SelectState) => state.selectTag 
);


