import { Component, AfterContentInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { TalkState } from '../../app/ng/reducers/states';

import * as TalkReducer from '../../app/ng/reducers/talk.reducer';
import { Observable } from 'rxjs/Observable';
import { TalkInfo, PageInfo } from '../../app/ng/models/srtModel';

import {Clipboard} from 'ts-clipboard';

import * as StringUtil from '../../app/commons/StringUtil';

import * as _ from 'lodash';


@Component({
  selector: 'page-list',
  templateUrl: 'list.html'
})
export class ListPage implements AfterContentInit {
 
  talks$: Observable<TalkInfo>;

  talkInfos = [];
  allTalkInfos = [];
  pageInfos: PageInfo[] = [];
  pageMap: Map<string, TalkInfo[]> = new Map();

  rangeDate: string = '';

  constructor(public navCtrl: NavController, private talkStore: Store<TalkState>) {
   
    this.talks$ = talkStore.select(TalkReducer.getTalk);
  }

  ngAfterContentInit() {



    this.talks$.subscribe(
      (talkInfo: TalkInfo) => {

        if(talkInfo != undefined && talkInfo != null) {
          this.pageMap.clear();
          this.talkInfos = [];
          this.talkInfos.push(talkInfo);
  
          this.allTalkInfos.push(talkInfo);
          this.processOther(talkInfo);
          this.processDatePage(talkInfo);
        }
       

        // console.log(talkInfo.talkList[0]);
      }
    );
  }

  getBackgroundColor(talk: TalkInfo) {
    console.log('11122212');
    if(talk[1].op[1].author === talk.youId) {
      return {background: '#f4f1c0'};
    } else {
      return {background: '#eaeaea'};
    }
  }

  processOther(talk: TalkInfo) {
    

    this.rangeDate = this.getTotalRange(talk);
  }

  getTotalRange(talk: TalkInfo): string {

    if(talk.talkList == undefined || talk.talkList == null || talk.talkList.length <= 0) {
      return '';
    }

    let start = StringUtil.convertDate((talk.talkList[0])[1].timestamp);
    let end = StringUtil.convertDate((talk.talkList[talk.talkList.length-1])[1].timestamp);

    let fileName = '';
    fileName += start;
    fileName += ' ~ ';
    fileName += end;

    return fileName;
  }

  processDatePage(talkInfo: TalkInfo) {

    if(talkInfo == undefined || talkInfo == null) {
      return;
    }

    const td = this.getTotalRange(talkInfo);

    this.pageMap.set(td, talkInfo.talkList);

    for(const talk of talkInfo.talkList) {

      const date = StringUtil.convertDate(talk[1].timestamp);

      if(this.pageMap.has(date.toString()) == false) {
        this.pageMap.set(date.toString(), []);
      }

      this.pageMap.get(date.toString()).push(talk);

    }

    const _this = this;
   
    this.pageMap.forEach((value, key) => {
      _this.pageInfos.push({
        label: key,
        key: key
      });
    });

    
  }

  optionSelected(event) {


    const tl  = this.pageMap.get(event);

    this.rangeDate = event;
    // console.log(tl);

    this.talkInfos[0].talkList = tl;

    // console.log(event);
  }

  ccopy() {
    

    let copyData = '';

    for (const talkInfo of this.allTalkInfos) {
      for(const it of talkInfo.talkList) {


        copyData += "<div style=background:";
        copyData += it[1].op[1].author == talkInfo.youId ? '#f4f1c0' : '#eaeaea';
        copyData += '>';
        copyData += it[1].op[1].author;
        copyData += ' - ';
        copyData += '<a href="https://steemit.com/' + it[1].op[1].json_metadata.tags[0] + '/@' + it[1].op[1].parent_author + '/' + it[1].op[1].parent_permlink + '"' + ' target="_blank">' +it[1].timestamp + '</a>';
        copyData += '<br/>';
       
        copyData += it[1].op[1].body;
        copyData += '</div>';
        copyData += '<br/>';
      }
    }

    // Clipboard.copy(copyData);

    this.download(copyData);

  }

  download(data) {

    const talk: TalkInfo = this.allTalkInfos[0];

    let fileName = '';
    fileName += talk.youId;
    fileName += '-';
    fileName += talk.friendId;

    fileName += '_'
    let start = StringUtil.convertDate((talk.talkList[0])[1].timestamp);
    let end = StringUtil.convertDate((talk.talkList[talk.talkList.length-1])[1].timestamp);

    fileName += start;
    fileName += '-';
    fileName += end;
    fileName += '.html';

    var csvData = new Blob([data], { type: 'text/html;charset=utf-8;' });
    var csvURL = null;
    if (navigator.msSaveBlob) {
      csvURL = navigator.msSaveBlob(csvData, fileName);
    } else {
      csvURL = window.URL.createObjectURL(csvData);
    }
    var tempLink = document.createElement('a');
    tempLink.href = csvURL;
    tempLink.setAttribute('download', fileName);
    tempLink.click();
    tempLink.remove();
  }

  

}




