import { Component, AfterContentInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { TalkState } from '../../app/ng/reducers/states';

import * as TalkReducer from '../../app/ng/reducers/talk.reducer';
import { Observable } from 'rxjs/Observable';
import { TalkInfo, PageInfo } from '../../app/ng/models/srtModel';

import {Clipboard} from 'ts-clipboard';

import * as StringUtil from '../../app/commons/StringUtil';

import * as _ from 'lodash';


@Component({
  selector: 'page-fulist',
  templateUrl: 'fulist.html'
})
export class FUListPage implements AfterContentInit {

  dataList: any;
  oriList: any;

  reverseIdx: number = -1;
  currentFlag: number = 0;

  sortOptions: Array<any> = [];
  sortLabel: string;
 
  constructor(public navCtrl: NavController,params: NavParams) {
   
    this.dataList = params.get('data');
    this.oriList = _.clone(this.dataList);
  }

  ngOnInit() {
    this.sortOptions.push({value:0 , label: '아이디 순'});
    this.sortOptions.push({value:1 , label: '내 코멘트'});
    this.sortOptions.push({value:2 , label: '내 보팅'});
    this.sortOptions.push({value:3 , label: '팔로우 코멘트'});
    this.sortOptions.push({value:4 , label: '팔로우 보팅'});

    this.sortLabel = this.sortOptions[0];
  }

  ngAfterContentInit() { 

   
   
  }

  optionSelected(event) {
    console.log(event);

    if (Number(event) == 0) {
      this.dataList = _.clone(this.oriList);
      return;
    }

    

    this.sortsort(Number(event));

    

    this.sortLabel = this.sortOptions[this.currentFlag];
  }

  clickReverse() {
    this.reverseIdx *= -1;

    this.sortsort(this.currentFlag);
  }


  sortsort(flag: number) {

    this.currentFlag = flag;
    
    this.dataList.sort((a: any, b: any) => {

      // let retValue = -1;

      let cmpA = this.getTime(a, flag);
      let cmpB = this.getTime(b, flag);

      if (cmpA == undefined && cmpB == undefined) {
        return this.reverseIdx * -1;
      }

      if (cmpA == undefined) {
        return this.reverseIdx * -1;
      }

      if (cmpB == undefined) {
        return this.reverseIdx;
      }

      // cmpA = StringUtil.convertDate(cmpA);
      // cmpB = StringUtil.convertDate(cmpB);

      const cmpAL = cmpA.split('-');
      const cmpBL = cmpB.split('-');

      let date_a = new Date(cmpAL[0], cmpAL[1] - 1, cmpAL[2]);
      let date_b = new Date(cmpBL[0], cmpBL[1] - 1, cmpBL[2]);

      if (date_a > date_b) {
        return this.reverseIdx;
      } 

      if (date_a < date_b) {
        return this.reverseIdx * -1;
      }
     

      // if (Number(cmpAL[1]) > Number(cmpBL[1])) {
      //   return retValue * -1;
      // }

      // if (Number(cmpAL[2]) >= Number(cmpBL[2])) {
      //   return retValue * -1;
      // }

      return this.reverseIdx;
            
      // if (a.count > b.count) {
      //   return 1 * flag;
      // } else {
      //   return -1 * flag;
      // }

    });

    
    
  }

  getTime(data: any, flag: number) {
    switch(flag) {
      case 1:
      return data.mecmt;
      
      case 2:
      return data.mevote;
      case 3:
      return data.fcmt;
      case 4:
      return data.fvote;
    }
  }

  genTable(dd: any) {
    let str = '';
    str = '<table class="tg">';
      str += '<tr>';
      str += '<th class="tg-us36">comment : ' + dd.mecmt + '</th>';
      str += '<th class="tg-us36" colspan="3" rowspan="2">' + dd.txt +'</th>';
      str += '<th class="tg-us36">comment : ' +  dd.fcmt + '</th>';
      str += '</tr>';
      str += '<tr>';
      str += '<td class="tg-us36">vote : ' + dd.mevote + '</td>';
      str += '<td class="tg-us36">vote : ' + dd.fvote + '</td>';
      str += '</tr>';
      str += '</table>';
  }
  

}




