import { createFeatureSelector, createSelector } from '@ngrx/store';
import * as fromActions from '../actions/srtActions';
import { TalkInfo } from '../models/srtModel';
import { TalkState } from './states';



export const initialState: TalkState = { talkInfo: null };

export function reducer(state = initialState, action: fromActions.TalkAction): TalkState {
  switch(action.type) {
    case fromActions.TALKS: {
      return {
          ...state,
          talkInfo: action.payload
        };
    }
    default: {
      return state;
    }
  }	
}

export const getTalkState = createFeatureSelector<TalkState>('talkState');

export const getTalk = createSelector(
    getTalkState, 
    (state: TalkState) => state.talkInfo 
);


