import { Component, AfterContentInit } from '@angular/core';
import { NavController, LoadingController, Loading, AlertController } from 'ionic-angular';

import * as steem from "steem";
import { Store } from '@ngrx/store';
import { ModeState, TalkState } from '../../app/ng/reducers/states';

import * as ModeReducer from '../../app/ng/reducers/mode.reducer';
import * as TalkReducer from '../../app/ng/reducers/talk.reducer';
import * as fromActions from '../../app/ng/actions/actions';
import * as srtFromActions from '../../app/ng/actions/srtActions';
import { TalkInfo, TalkHistoryInfo, FUInfo } from '../../app/ng/models/srtModel';

import { Storage } from '@ionic/storage';

import Showdown from "showdown";
import { FUListPage } from '../fulist/fulist';

import * as StringUtil from '../../app/commons/StringUtil';

var STORAGE_NAME = 'STORAGE_NAME';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})



export class HomePage implements AfterContentInit {

  youId: string;
  friendId: string;

  modes$ = null;
  loader: Loading;

  fuLoader: Loading;

  fuId: string;
  fuList: Array<string> = [];

  converter = new Showdown.Converter()

  
  histories: TalkHistoryInfo[];

  constructor(public navCtrl: NavController,
    private modeStore: Store<ModeState>,
    private talkStore: Store<TalkState>,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private storage: Storage) {

      this.modes$ = modeStore.select(ModeReducer.getMode);
    // this.talks$ = talkStore.select(TalkReducer.getTalk);
  }

  ngAfterContentInit() {
    this.storage.get(STORAGE_NAME).then((val) => {

      if(val == null) {
        this.storage.set(STORAGE_NAME, []);
        this.histories = [];
      } else {
        this.histories = val;
      }
      // console.log('histories', val);
    })
  }


  search() {

    console.log(this.youId);
    console.log(this.friendId);

    if (this.checkNull(this.youId) || this.checkNull(this.friendId)) {
      this.showAlert('당신 혹은 친구의 이름을 입력해주세요');
      return;
    }

    this.getYouInfo(this.youId);
  }

  searchHistory(his: TalkHistoryInfo) {
    this.youId = his.youId;
    this.friendId = his.friendId;

    this.getYouInfo(this.youId);
  }

  countHistory(youId: string, friendId: string) {
    let check: boolean = false;
    for(let ii = 0 ; ii < this.histories.length; ++ii) {
      if(this.histories[ii].youId == youId && this.histories[ii].friendId == friendId) {
        this.histories[ii].count += 1;
        check = true;
      }
    }

    if(check == false) {
      this.histories.push({
        youId: youId,
        friendId: friendId,
        count:1
      });
    }

    this.histories.sort((a: TalkHistoryInfo, b: TalkHistoryInfo) => {

      if ( a.count > b.count ) {
        return -1;
      } else {
        return 1;
      }
    });

    this.storage.set(STORAGE_NAME, this.histories);
  }

  checkNull(str) {
    if (str === undefined
      || str === null
      || str.length <= 0
      || str == '') {
      return true;
    }
    return false;
  }

  showAlert(msg: string) {
    let alert = this.alertCtrl.create({
      title: '경고!',
      subTitle: msg,
      buttons: ['응']
    });
    alert.present();
  }

  getYouInfo(youId: string) {

    this.loader = this.loadingCtrl.create({
      content: "Please wait...",
      // duration: 3000
    });

    this.loader.present();

    this.countHistory(this.youId, this.friendId);

    const _this = this;
    steem.api.getAccountHistory(youId,-1,10000,(err, result) => {

      if (err != null) {
        _this.showAlert('에러났어요');
        return;
      }

      _this.parseTalk(result);

    });
  }

  parseTalk(result) {


    const list = result;

    const realList = new Array();
    const talkList: Array<any> = new Array();
    for(let i = 0 ; i < list.length; ++i) {
        const data = list[i];

        // console.log(data[0]);
        
        if(data[1].op[0] == undefined || data[1].op[0] == null || data[1].op[0] != 'comment') {
            continue;
        }

        const cmtData = data[1].op[1];

        if(data[1].op[1].parent_author == undefined
             || data[1].op[1].parent_author == null
             || data[1].op[1].parent_author == ''
            ) {
            continue;
        }

        
        if(cmtData.parent_author == this.friendId || cmtData.author == this.friendId) {

            // ddd += cmtData.author;
            // ddd += '\r\n';
            // ddd += cmtData.body;
            // ddd += '\r\n';
            // json_metadata
            try {
              data[1].op[1].json_metadata = JSON.parse(data[1].op[1].json_metadata);
            } catch(e) {
              
            }
            
            
            data[1].op[1].body = this.converter.makeHtml(data[1].op[1].body);
            talkList.push(data);
            
        }
        
    }

    // this.viewTalkList = talkList;

    if(talkList.length <= 0) {
      this.showAlert(this.youId + ' - ' + this.friendId + ' 간에 대화내용이 없어요!');
      this.loader.dismissAll();
      return;
    }

    const ti: TalkInfo = {
      talkList : talkList,
      youId : this.youId,
      friendId : this.friendId
    }

    this.loader.dismissAll();

    this.talkStore.dispatch(new srtFromActions.TalkAction(ti));

    this.modeStore.dispatch(new fromActions.ModeAction('talk'));

    

  }

  searchFollow() {
    console.log('searchFollow - ' + this.fuId);

    if (this.checkNull(this.fuId) ) {
      this.showAlert('당신의 이름은..없나요?');
      return;
    }

    this.fuLoader = this.loadingCtrl.create({
      content: "Please wait...",
      // duration: 3000
    });

    this.fuLoader.present();

    this.getFollowing(this.fuId);
  }

  getFollowing(fuId: string) {

    // steem.api.getFollowCountAsync(fuId)
    // .then(function (result) {

    //   console.log(result.following_count);
    // });

    const list: Array<any> = [];

    // this.reqFollowing(fuId, '', 1000, list);
    const _this = this;
    const fList: Array<string> = [];
    this.reqAccountHistory(fuId, -1, list, function(lll) {
      // console.log(list);

      _this.reqFollowing(fuId, '', 1000, fList, function(ttt) {
        _this.process(list, fList);
      });

    })

  }

  process(ahList: Array<any>, fList: Array<string>) {

    const ahMap: Map<string, FUInfo> = new Map();

    for(let i = 0 ; i < ahList.length; ++i) {
      const data = ahList[i];

      if (data[1].op[0] == undefined || data[1].op[0] == null || (data[1].op[0] != 'comment' && data[1].op[0] != 'vote') ) {
        continue;
      }

      const realData = data[1].op[1];
      realData.timestamp = data[1].timestamp;

      

      if(data[1].op[0] == 'comment') {

        const fuInfo: FUInfo = this.getMapData(realData.author, realData.parent_author, ahMap);

        if(realData.json_metadata.length > 0) {
          try {
            realData.json_metadata = JSON.parse(realData.json_metadata);

            if(realData.json_metadata.tags.length > 0) {
              realData.jm = true;
            } else {
              console.log(realData);
            }

            
          } catch {
            console.log(realData);
          }
          
        }
        

        fuInfo.comment = realData;
        // fuInfo.timestamp =  data[1].timestamp;

        this.setMapData(realData.author, realData.parent_author, fuInfo, ahMap);

      } else if(data[1].op[0] == 'vote') {

        const fuInfo: FUInfo = this.getMapData(realData.voter, realData.author, ahMap);
        fuInfo.vote = realData;
        // fuInfo.timestamp =  data[1].timestamp;
        
        this.setMapData(realData.voter, realData.author, fuInfo, ahMap);
      }


    }

    this.fuLoader.dismiss();

    // console.log(ahMap);
    this.processView(ahMap, fList);

  }

  processView(map: Map<string, FUInfo>, fList: Array<string>) {

    
    const testViewList: Array<string> = [];
    const tesDataList: Array<any> = [];

    for(let i = 0 ; i < fList.length; ++i) {
      const ff = fList[i];
      const fuInfoMe = this.getMapData(this.fuId, ff, map);
      const fuInfoFollow = this.getMapData(ff, this.fuId, map);
      
      // let str = 'comment : ';
      // str += fuInfoMe.comment.timestamp
      // str += ' , vote : ';
      // str += fuInfoMe.vote.timestamp;
      // str += ' = ';
      // str += this.fuId;
      // str += ' ♡  ';
      // str += ff;
      // str += ' = ';
      // str += 'comment : ';
      // str += fuInfoFollow.comment.timestamp;
      // str += ' , vote : ';
      // str += fuInfoFollow.vote.timestamp;
      // // console.log(str);

      // str = '<table class="tg">';
      // str += '<tr>';
      // str += '<th class="tg-us36">comment : ' + fuInfoMe.comment.timestamp + '</th>';
      // str += '<th class="tg-us36" colspan="3" rowspan="2">' + this.fuId + ' ♡ ' + ff +'</th>';
      // str += '<th class="tg-us36">comment : ' +  fuInfoFollow.comment.timestamp + '</th>';
      // str += '</tr>';
      // str += '<tr>';
      // str += '<td class="tg-us36">vote : ' + fuInfoMe.vote.timestamp + '</td>';
      // str += '<td class="tg-us36">vote : ' + fuInfoFollow.vote.timestamp + '</td>';
      // str += '</tr>';
      // str += '</table>';


      // StringUtil.convertDate(cmpB)
      const dd = {
        mecmt: StringUtil.convertDate(fuInfoMe.comment.timestamp),
        mevote: StringUtil.convertDate(fuInfoMe.vote.timestamp),
        txt: this.fuId + ' ♡ ' + ff,
        fcmt: StringUtil.convertDate(fuInfoFollow.comment.timestamp),
        fvote: StringUtil.convertDate(fuInfoFollow.vote.timestamp),
        meobj: fuInfoMe,
        fobj: fuInfoFollow
      }

      // testViewList.push(str);
      tesDataList.push(dd);
    }

    this.navCtrl.push(FUListPage, {
      data: tesDataList
    });

  }

  setMapData(sender: string, receiver:string, fuInfo: FUInfo, map: Map<string, FUInfo>) {

    const key:string = sender + '-' + receiver;

    map.set(key, fuInfo);

  }

  getMapData(sender: string, receiver: string, map: Map<string, FUInfo>): FUInfo {

    const key:string = sender + '-' + receiver;

    if(map.has(key)) {
      return map.get(key);
    }



    return {sender:sender, receiver:receiver, comment: {}, vote:{}};
  }


  reqAccountHistory(id: string, from: number, list: Array<any>, func: Function) {

    const _this = this;
    steem.api.getAccountHistory(id, from,10000,(err, result) => {

      if (err != null) {
        return;
      }

      list.push.apply(list, result);

      // if(result.length >= 10000) {
      //   _this.reqAccountHistory(id, result[0][0], list, func);
      // } else {
        func(list);
      // }

      // _this.parseTalk(result);

    });

  }

  reqFollowing(user: string, start: string, size: number, list: Array<string>, func: Function) {


    const _this = this;

    steem.api.getFollowingAsync(user, start, 'blog', size)
          .then(function (result) {
    
            for (let i = 0; i < result.length; ++i) {
              list.push(result[i].following);
            }

            if (result.length >= size) {
              _this.reqFollowing(user, list[list.length-1], size, list, func);
            } else {
              // _this.fuList = list;
              // console.log('totla fu - ' + list.length);
              func(list);

            }

          });

  }


  

}



