import { Action } from '@ngrx/store';
import { PostInfo, AuthorInfo } from '../models/model';

export const POSTS = 'POSTS';
export const AUTHOR = 'AUTHOR';
export const SELECTTAG = 'SELECTTAG';
export const GETEND = 'GETEND';
export const MODE = 'GETEND';


export class PostAction implements Action {
  readonly type = POSTS;

  constructor(public payload: PostInfo[]) {}
}

export class AuthorAction implements Action {
  readonly type = AUTHOR;

  constructor(public payload: AuthorInfo[]) {}
}

export class SelectAction implements Action {
  readonly type = SELECTTAG;

  constructor(public payload: String) {}
}

export class GetEndAction implements Action {
  readonly type = GETEND;

  constructor(public payload: Boolean) {}
}

export class ModeAction implements Action {
  readonly type = MODE;

  constructor(public payload: String) {}
}


export type All = PostAction | AuthorAction | SelectAction | GetEndAction | ModeAction; 


