import { ActionReducerMap, ActionReducer, MetaReducer } from '@ngrx/store';
import { AppState } from './states';
import * as postReducer from './post.reducer';
import * as authorReducer from './author.reducer';
import * as selectReducer from './select.reducer';
import * as getEndReducer from './getend.reducer';
import * as modeReducer from './mode.reducer';
import * as talkReducer from './talk.reducer';
// import { environment } from '../../environments/environment';

export const reducers: ActionReducerMap<AppState> = {
  postState: postReducer.reducer,
  authorState: authorReducer.reducer,
  selectState: selectReducer.reducer,
  getEndState: getEndReducer.reducer,
  modeState: modeReducer.reducer,
  talkState: talkReducer.reducer
};

export function logger(reducer: ActionReducer<AppState>): ActionReducer<AppState> {
  return function(state: AppState, action: any): AppState {
    console.log('state', state);
    console.log('action', action);
    return reducer(state, action);
  };
}

